import {Component} from '@angular/core';
import {formModel} from '../form.model'
import {ActivatedRoute,Router} from '@angular/router';
import { RegisterService } from '../register.service';

@Component({
    selector:'register-app',
    templateUrl:`./Register.component.html`,
    styleUrls:['./Register.component.css']
})
export class RegisterComp {
    model = new formModel('','','','','',1);
    constructor(private router:Router,private activatedRout:ActivatedRoute
      ,private obj:RegisterService){
    }
    newcontact;
    data;
    data1;
    get currentform(){
      this.data = JSON.stringify(this.model);
      return JSON.stringify(this.model)
    }
    Next(f){
      this.data1=f.value;
      this.router.navigate(["/edit"]);
      this.obj.data1(this.data1)
    }
}